



















import Vue from "vue";
import { blogDataRenderer } from "../blogToHtml";

export default Vue.extend({
  name: "LiveBlogRenderer",
  data(): Record<string, string> {
    return {
      title: "",
      subtitle: "",
      source: "",
    };
  },
  computed: {
    contents: function (): { header: string; body: string } {
      return blogDataRenderer(
        this.title,
        this.subtitle,
        "You",
        new Date().toString(),
        this.source
      );
    },
  },
});
